.player-container{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control{
    width: 50%;
    display: flex;
    align-items: center;
    input{
        width: 100%;
        cursor: pointer;
        -webkit-appearance: none;
        background: transparent;
    }
    p{
        padding: 0 1rem;
    }
    @media (max-width: 950px) {
        width: 80%;
      }
}

.play-control{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 15%;
    svg{
        cursor: pointer;
    }
    @media (max-width: 950px) {
        width: 45%;
      }
}

input[type="range"]:focus{
    outline: none;
}

input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
}

input[type="range"]::-moz-range-thumb{
    -webkit-appearance: none;
    background: transparent;
    border: none;
}

.track{
    background: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track{
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}